.mainBlock {
  height: 100vh;
  text-align: center;
}

.textBlock {
  width: 350px;
  font-size: 24px;
  color: white;
  z-index: 2;
}

.photoContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;

  &::before,
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    border: 1px solid rgb(245, 245, 245);
    transition: all 1.3s ease-out;
  }

  &::before {
    border: 1px solid rgb(255, 255, 255);
    top: -15px;
    left: -15px;
  }

  &::after {
    border: 1px solid rgb(255, 255, 255);
    bottom: -15px;
    right: -15px;
  }

  &:hover {
    &::before {
      border: 1px solid rgb(0, 0, 0);
      top: 15px;
      left: 15px;

    }

    &::after {
      border: 1px solid rgb(0, 0, 0);
      bottom: 15px;
      right: 15px;
    }
  }
}


.photo {
  width: 100%;
  height: 450px;
  z-index: 1;
  transition: 1s ease-in-out;
}

.photo:hover {
  -o-transform: translate(-20px, 20px);
  -ms-transform: translate(-20px, 20px);
  -moz-transform: translate(-20px, 20px);
  -webkit-transform: translate(-20px, 20px);
  transform: translate(-20px, 20px);
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0.3;
}

